// I wanted to use the .sass format, but there's apparently
// absolutely no way to get VSCode's builtin color picker to work with it :(((

#real-header-not-react-one {
    height: 2.5rem
}

nav {
    z-index: 10;
    position: fixed;
    top: -2rem;
    left: 1vw;
    width: 98vw;
    background-color: rgba(43,114,35,.7);
    border-radius: 3px;
    border-style: solid;
    border-color: #0e62a7;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;

    transition: all 1s ease;
    &:hover {
        top: -.1rem;
    }
}

.navlink {
    font-size: 36px;
    transition: all 1s ease;
    text-decoration: none;
    // background-color: rgba(147, 158, 25, 0.329);
    background-image: 
        radial-gradient(ellipse at center, rgba(149, 158, 25, 0.66) 1%,rgba(149, 158, 25, 0) 100%);
    color: #203;

    &:hover {
        background-image: 
            radial-gradient(ellipse at center, rgba(15, 92, 44, 1) 1%,rgba(15, 92, 44, 0) 100%);
        color: #199e97;
        // background-color: #203;
        // font-size: 48px;
        text-shadow: 12px 12px 3 #0c3150
    }
}

.beneath-nav-bar {
    position: absolute;
    top: 48px;
    width: 100vw
}

#about-academy {
    // background-color: #010214;

    // * {
    //     color: #61f1f1;
    // }

    #academy-name, #blurb, h2, #mission {
        text-align: center;
    }

    #academy-name {
        font-size: 42px
    }

    #blurb {
        font-size: 30px
    }

    h2 {
        font-size: 36px
    }

    #mission-statement {
        display: flex;
        flex-wrap: true;
        justify-content: center;
        font-size: 1.5rem
    }

    #false-mission {
        text-decoration: line-through
    }

    #note-to-self {
        font-style: italic
    }
        
}

