#space-img {
    position: fixed;
    top: 0;
    left: 0;
    min-height: 100vh;
    min-width: 100vw;
    z-index: -1
}


#selected-campus-name {
    font-size: 48px;
    text-align: center
}

#selected-campus-description {
    font-size: 24px;
}

