#student-table-col-names {
    display: flex;
    justify-content: space-around
}

#student-table-rows {
    left: 5vw;
    width: 90vw;
    display: flex;
    flex-direction: column;
    list-style-type: decimal
}

.student-table-row {
    display: flex;
    justify-content: space-between;
    font-size: 24px
}

#joke-question, #joke-answer {
